"use client";

import Link from "next/link";
import { usePathname } from "next/navigation";
import { Button } from "@/components/ui/button";
import { Volume2, VolumeX, PenSquare, Layers, UserCircle2 } from "lucide-react";
import { useState } from "react";
import { cn } from "@/lib/utils";

export function Navbar() {
  const pathname = usePathname();
  const [isMuted, setIsMuted] = useState(false);

  return (
    <nav className="sticky top-0 z-50 w-full border-b bg-background/95 backdrop-blur supports-[backdrop-filter]:bg-background/60">
      <div className="container flex h-16 items-center px-4">
        <div className="flex gap-6 md:gap-10">
          <Link href="/" className="flex items-center space-x-2">
            <span className="inline-block font-bold text-xl bg-gradient-to-r from-pink-500 to-rose-500 bg-clip-text text-transparent">
              Unvalentine
            </span>
          </Link>
        </div>

        <div className="flex flex-1 items-center justify-end space-x-4">
          <nav className="flex items-center space-x-2">
            <Button
              variant={pathname === "/create" ? "secondary" : "ghost"}
              size="sm"
              className="w-9 px-0"
              asChild
            >
              <Link href="/create">
                <PenSquare className="h-5 w-5" />
                <span className="sr-only">Create Note</span>
              </Link>
            </Button>

            <Button
              variant={pathname === "/notes" ? "secondary" : "ghost"}
              size="sm"
              className="w-9 px-0"
              asChild
            >
              <Link href="/notes">
                <Layers className="h-5 w-5" />
                <span className="sr-only">All Notes</span>
              </Link>
            </Button>

            <Button
              variant="ghost"
              size="sm"
              className="w-9 px-0"
              onClick={() => setIsMuted(!isMuted)}
            >
              {isMuted ? (
                <VolumeX className="h-5 w-5" />
              ) : (
                <Volume2 className="h-5 w-5" />
              )}
              <span className="sr-only">Toggle Sound</span>
            </Button>

            <Button variant="ghost" size="sm" className="w-9 px-0">
              <UserCircle2 className="h-5 w-5" />
              <span className="sr-only">Anonymous User</span>
            </Button>
          </nav>
        </div>
      </div>
    </nav>
  );
}
